:root {
    --founder-regular: 'Founder-Regular', sans-serif;
    --founder-regular-italic: 'Founder-RegularItalic', sans-serif;
    --founder-bold: 'Founder-Bold', sans-serif;
    --founder-bold-italic: 'Founder-BoldItalic', sans-serif;
    --founder-black: 'Founder-Black', sans-serif; /* Note: Adjust the actual font file name accordingly */
    --founder-black-italic: 'Founder-BlackItalic', sans-serif; /* Note: Adjust the actual font file name accordingly */
    --founder-light: 'Founder-Light', sans-serif;
    --founder-light-italic: 'Founder-LightItalic', sans-serif;
    --founder-medium: 'Founder-Medium', sans-serif;
    --founder-medium-italic: 'Founder-MediumItalic', sans-serif;
    --founder-semi-bold: 'Founder-SemiBold', sans-serif;
    --founder-semi-bold-italic: 'Founder-SemiBoldItalic', sans-serif;
}

/* styles.css */

@font-face {
    font-family: 'Founder-Regular';
    src: url('../Founders_Grotesk/FoundersGrotesk-Regular.otf');
    font-weight: normal;
    font-style: normal; /* Regular style */
}

@font-face {
    font-family: 'Founder-RegularItalic';
    src: url('../Founders_Grotesk/FoundersGrotesk-RegularItalic.otf');
    font-weight: normal;
    font-style: italic;
}




@font-face {
    font-family: 'Founder-Bold';
    src: url('../Founders_Grotesk/FoundersGrotesk-Bold.otf');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'Founder-BoldItalic';
    src: url('../Founders_Grotesk/FoundersGrotesk-BoldItalic.otf');
    font-weight: bold;
    font-style: italic;
}

@font-face {
    font-family: 'Founder-Light';
    src: url('../Founders_Grotesk/FoundersGrotesk-Light.otf');
    font-weight: lighter; /* Use 'lighter' for a lighter weight */
    font-style: normal;
}

@font-face {
    font-family: 'Founder-LightItalic';
    src: url('../Founders_Grotesk/FoundersGrotesk-LightItalic.otf');
    font-weight: lighter;
    font-style: italic;
}

@font-face {
    font-family: 'Founder-Medium';
    src: url('../Founders_Grotesk/FoundersGrotesk-Medium.otf');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Founder-MediumItalic';
    src: url('../Founders_Grotesk/FoundersGrotesk-MediumItalic.otf');
    font-weight: normal;
    font-style: italic;
}

@font-face {
    font-family: 'Founder-SemiBold';
    src: url('../Founders_Grotesk/FoundersGrotesk-Semibold.otf');
    font-weight: 600; /* Use '600' for a semi-bold weight */
    font-style: normal;
}

@font-face {
    font-family: 'Founder-SemiBoldItalic';
    src: url('../Founders_Grotesk/FoundersGrotesk-SemiboldItalic.otf');
    font-weight: 600;
    font-style: italic;
}
