:root {
    --garnett-regular: 'Garnett-Regular', sans-serif;
    --garnett-regular-italic: 'Garnett-RegularItalic', sans-serif;
    --garnett-bold: 'Garnett-Bold', sans-serif;
    --garnett-bold-italic: 'Garnett-BoldItalic', sans-serif;
    --garnett-black: 'Garnett-Black', sans-serif;
    --garnett-black-italic: 'Garnett-BlackItalic', sans-serif;
    --garnett-light: 'Garnett-Light', sans-serif;
    --garnett-light-italic: 'Garnett-LightItalic', sans-serif;
    --garnett-medium: 'Garnett-Medium', sans-serif;
    --garnett-medium-italic: 'Garnett-MediumItalic', sans-serif;
    --garnett-semi-bold: 'Garnett-SemiBold', sans-serif;
    --garnett-semi-bold-italic: 'Garnett-SemiBoldItalic', sans-serif;
  }
  

/* styles.css */

@font-face {
    font-family: 'Garnett-Regular';
    src: url('../Garnett/Garnett-Regular.ttf');
    font-weight: 400;
    font-style: normal; /* Regular style */
}

@font-face {
    font-family: 'Garnett-RegularItalic';
    src: url('../Garnett/Garnett-RegularItalic.ttf');
    font-weight: normal;
    font-style: italic;
}

@font-face {
    font-family: 'Garnett-Black';
    src: url('../Garnett/Garnett-Black.ttf');
    font-weight: bold; /* Use 'bold' for a bold font */
    font-style: normal; /* Regular style */
}

@font-face {
    font-family: 'Garnett-BlackItalic';
    src: url('../Garnett/Garnett-BlackItalic.ttf');
    font-weight: bold;
    font-style: italic;
}

@font-face {
    font-family: 'Garnett-Bold';
    src: url('../Garnett/Garnett-Bold.ttf');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'Garnett-BoldItalic';
    src: url('../Garnett/Garnett-BoldItalic.ttf');
    font-weight: bold;
    font-style: italic;
}

@font-face {
    font-family: 'Garnett-Light';
    src: url('../Garnett/Garnett-Light.ttf');
    font-weight: lighter; /* Use 'lighter' for a lighter weight */
    font-style: normal;
}

@font-face {
    font-family: 'Garnett-LightItalic';
    src: url('../Garnett/Garnett-LightItalic.ttf');
    font-weight: lighter;
    font-style: italic;
}

@font-face {
    font-family: 'Garnett-Medium';
    src: url('../Garnett/Garnett-Medium.ttf');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Garnett-MediumItalic';
    src: url('../Garnett/Garnett-MediumItalic.ttf');
    font-weight: normal;
    font-style: italic;
}

@font-face {
    font-family: 'Garnett-SemiBold';
    src: url('../Garnett/Garnett-Semibold.ttf');
    font-weight: 600; /* Use '600' for a semi-bold weight */
    font-style: normal;
}

@font-face {
    font-family: 'Garnett-SemiBoldItalic';
    src: url('../Garnett/Garnett-SemiboldItalic.ttf');
    font-weight: 600;
    font-style: italic;
}
